//****************************************
// Graph Tools Element
// DOM Registration: No
// National Instruments Copyright 2017
//****************************************

/**
## ni-graph-tools

> A custom element that provides a graph tools "legend".

Use it to add navigation controls to a graph, chart or intensity graph element.
It provides buttons to switch between pan mode, box zoom mode or to lock navigation.

It also provides a scaleOnce button which resets the axes to the range of the data.

Example:
```html
<ni-cartesian-graph graph-ref="g1" value="[[2, 3, 5, 7, 11, 13, 17, 19],
                          [0, 1, 1, 2, 3, 5, 8, 13]]">
  <ni-cartesian-axis show label="Index" axis-position="bottom"></ni-cartesian-axis>
  <ni-cartesian-axis show label="Value" axis-position="left"></ni-cartesian-axis>
</ni-cartesian-graph>

<ni-graph-tools graph-name="g1" mode="pan"></ni-graph-tools>
```
*/

(function() {
    'use strict';

    var ModeEnum = Object.freeze({
        LOCKED: 'locked',
        PAN: 'pan',
        ZOOM: 'zoom'
    });

    var MODE_ENUM = ModeEnum;
    var $ = NationalInstruments.Globals.jQuery;
    var NI_SUPPORT = NationalInstruments.HtmlVI.NISupport;

    var navigationStates = {};
    navigationStates[MODE_ENUM.LOCKED] = [true, false, false];
    navigationStates[MODE_ENUM.PAN] = [false, true, false];
    navigationStates[MODE_ENUM.ZOOM] = [false, false, true];

    class GraphTools extends NationalInstruments.HtmlVI.Elements.GraphLegend {
        /**### ni-graph-tools properties*/

        /// include ./ni-graph-legend.js
        static get properties() {
            const props = Object.assign({}, super.properties, {
                /** **mode** (string) - the mode of the graph tools. Default: 'pan'.
                Accepted values: 'locked', 'pan', 'zoom'*/
                'mode': {
                    value: MODE_ENUM.PAN,
                    type: 'string'
                }
            });

            return props;
        }

        created() {
            super.created();
            this._parentGraph = undefined;
        };

        attachToGraph(graphElement) {
            if (graphElement.isReady) {
                this._parentGraph = graphElement;
                graphElement.registerGraphTools(this);
            }
        };

        /* The result of user interactions (pan, zoom, ..) are often modified axes
        parameters in the flot datastructures.
        When needed, call this function to update the ni-cartesian-axis elements to have te new values */
        updateCartesianAxesFromFlot() {
            this._parentGraph.axes.forEach(function(axis) {
                if (axis instanceof JQX.CartesianAxis) {
                    axis.syncWithFlot();
                }
            });
        };

        attached() {
            super.attached();

            var childElement,
                that = this,
                applySelection = function(axis, selection) {
                    var opts = axis.options,
                        offsetBelow = selection.from - axis.min,
                        offsetAbove = selection.to - axis.max;

                    if (opts.offset) {
                        opts.offset.below += offsetBelow;
                        opts.offset.above += offsetAbove;
                    } else {
                        opts.offset = {
                            above: offsetAbove,
                            below: offsetBelow
                        };
                    }
                };

            childElement = document.createElement('div');
            childElement.id = 'div' + NI_SUPPORT.uniqueId();
            childElement.style.width = '100%';
            childElement.style.height = '100%';
            this.appendChild(childElement);
            this.table = document.createElement('table');
            this.table.className = 'ni-graph-tools-box';
            childElement.appendChild(this.table);

            this.addEventListener('ni-cartesian-graph-box-selected', function(evt) {
                // a rectangle selection was made on the graph, zoom into that selection
                // info about the selection box is passed in the event details (ranges)
                var ranges = evt.detail.originalSource.ranges;
                var plot = that._parentGraph.graph;

                // set the visible ranges for x axes
                // TODO cipix: fix this for multiple axes
                $.each(plot.getXAxes(), function(_, axis) {
                    if (axis.options.disableZoom !== true) {
                        applySelection(axis, ranges.xaxis);
                    }
                });

                // set the visible ranges for y axes
                // TODO cipix: fix this for multiple axes
                $.each(plot.getYAxes(), function(_, axis) {
                    if (axis.options.disableZoom !== true) {
                        applySelection(axis, ranges.yaxis);
                    }
                });

                // redraw plot
                plot.setupGrid();
                plot.draw();
                plot.clearSelection();

                this.updateCartesianAxesFromFlot();
            });

            this.addEventListener('ni-cartesian-graph-interaction', function() {
                this.updateCartesianAxesFromFlot();
            });

            this.addEventListener('ni-cartesian-graph-reCenter', function() {
                that._parentGraph.axes.forEach(function(axis) {
                    if (axis.scaleOnce) {
                        axis.scaleOnce();
                    }
                });

                var plot = that._parentGraph.graph;
                plot.setupGrid();
                plot.draw();

                this.updateCartesianAxesFromFlot();
            });

            this.createRow(this.table);

            var graph = this.findGraph();
            if (graph !== null && graph instanceof NationalInstruments.HtmlVI.Elements.CartesianGraphBase) {
                this.attachToGraph(graph);
            }
        };

        createRow(tbl) {
            var that = this;
            var tr, td1, td2, td3, td4, navNone, btnPan, btnZoom, btnZoomOut;

            function setNavButtonsState(locked, pan, zoom) {
                $(navNone).jqxToggleButton({
                    disabled: locked
                });
                $(btnPan).jqxToggleButton({
                    disabled: pan
                });
                $(btnZoom).jqxToggleButton({
                    disabled: zoom
                });
            }

            function setMode(mode) {
                var states = navigationStates[mode];

                if (states) {
                    setNavButtonsState.apply(null, states);
                    that.mode = mode;
                }
            }

            tr = document.createElement('tr');
            tbl.appendChild(tr);
            td1 = document.createElement('td');
            td1.className = 'ni-button-box';
            tr.appendChild(td1);
            navNone = document.createElement('button');
            navNone.type = 'button';
            navNone.className = 'ni-locked-icon ni-button';
            $(navNone).jqxToggleButton();
            $(navNone).on('click', function() {
                setMode(MODE_ENUM.LOCKED);
            });
            td1.appendChild(navNone);

            td2 = document.createElement('td');
            td2.className = 'ni-button-box';
            tr.appendChild(td2);
            btnPan = document.createElement('button');
            btnPan.type = 'button';
            btnPan.className = 'ni-pan-icon ni-button';
            $(btnPan).jqxToggleButton();
            $(btnPan).on('click', function() {
                setMode(MODE_ENUM.PAN);
            });
            td2.appendChild(btnPan);

            td3 = document.createElement('td');
            td3.className = 'ni-button-box';
            tr.appendChild(td3);
            btnZoom = document.createElement('button');
            btnZoom.type = 'button';
            btnZoom.className = 'ni-zoom-region-icon ni-button';
            $(btnZoom).on('click', function() {
                setMode(MODE_ENUM.ZOOM);
            });
            td3.appendChild(btnZoom);

            setMode(this.mode); // initialize the "navigation radio butons" in the default mode
            td4 = document.createElement('td');
            td4.className = 'ni-button-box';
            tr.appendChild(td4);
            btnZoomOut = document.createElement('button');
            btnZoomOut.type = 'button';
            btnZoomOut.className = 'ni-zoom-out-icon ni-button';
            $(btnZoomOut).jqxButton({
                disabled: false
            });
            $(btnZoomOut).on('click', function() {
                that._parentGraph.axes.forEach(function(axis) {
                    if (axis.scaleOnce) {
                        axis.scaleOnce();
                    }
                });

                var plot = that._parentGraph.graph;
                plot.setupGrid();
                plot.draw();
            });
            td4.appendChild(btnZoomOut);
        };

        allowsScrollWheelZoom() {
            return this.mode === MODE_ENUM.PAN || this.mode === MODE_ENUM.ZOOM;
        };

        allowsPan() {
            return this.mode === MODE_ENUM.PAN;
        };

        sendEventToParentGraph(name) {
            var eventConfig;

            if (this._parentGraph !== undefined) {
                eventConfig = {
                    bubbles: true,
                    cancelable: true,
                    detail: {
                        element: this
                    }
                };

                this._parentGraph.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);

            switch (propertyName) {
                case 'graphName':
                    // should never change
                    break;
                case 'mode':
                    this.sendEventToParentGraph('ni-graph-tools-changed');
                    break;
                default:
                    break;
            }
        };
    };

    JQX('ni-graph-tools', GraphTools);
}());
