
(function() {
    'use strict';

    class GraphLegend extends window.JQX.BaseElement {
        static get properties() {
            const props = Object.assign({}, super.properties, {
                /** **graph-name** - the graph id to connect to. Default: ''*/
                'graphName': {
                    value: '',
                    type: 'string'
                },
                /** **is-in-edit-mode** - makes the legend non-interactive*/
                'isInEditMode': {
                    value: false,
                    type: 'boolean'
                }
            });

            return props;
        }

        findGraph() {
            return document.querySelector('[ni-control-id=\'' + this.graphName + '\']') ||
                document.querySelector('[graph-ref=\'' + this.graphName + '\']');
        }

        template() {
            return '';
        }
    };

    NationalInstruments.HtmlVI.Elements.GraphLegend = GraphLegend;
}());
